@media screen and (max-width: 996px) {
  .carousel-item {
    height: 400px;
  }

  #person {
    aspect-ratio: 1/1;
    object-fit: cover;
  }
}

@media screen and (max-width: 768px) {
  :root {
    --font-base: 0.8rem;
    --font-lg-base: 0.9rem;
    --font-lg: 1.23rem;
    --font-xlg: 1.4rem;
    --font-sm: 0.7rem;
    --font-xsm: 0.5rem;
    --font-xxsm: 0.3rem;
    --font-carousel-heading: 2.8rem;
    --font-carousel-subHeading: 1.15rem;
  }

  .cbadge {
    height: 1.578rem;
  }
  .cbadge::after,
  .cbadge::before {
    height: 1.55rem;
  }

  .carousel-item {
    height: 370px;
  }

  #recommended_img {
    width: 200px;
  }
}

@media screen and (max-width: 576px) {
  .carousel h1 {
    font-size: 2rem;
    line-height: 2.3rem;
  }
  .caption {
    margin: 0 1.8rem 1.8rem;
  }
  .carousel-item {
    height: 415px;
  }

  #carousel_inner_image,
  #carousel_inner_video {
    max-width: 100vw;
  }

  #bubble{
    width: 85vw;
  }
}

@media screen and (max-width: 470px) {
  .param {
    font-size: small;
  }

  .media_name,
  .media_year {
    font-size: large;
    line-height: 1.7rem;
  }

  #bubble{
    width: 95vw;
  }
}
